@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200;400&display=swap);
:root {
  --primary: #5c6784;
  --gold: #f6ca83;
  --dark: #000022;
}

body {
  color: #000022;
  color: var(--dark);
  font-family: "Montserrat", sans-serif;
  margin: 0;
  padding: 0;
}

nav {
  background: var(--primary);
  border-bottom: 3px solid var(--dark);
  color: white;
  text-align: center;
}

nav h1 {
  margin: 0;
  padding: 50px;
}

.employee {
  border: 1px solid var(--gold);
  border-radius: 5px;
  padding: 25px;
  text-align: center;
}

.employee h3 {
  margin: 0;
}

.employee h4 {
  font-weight: 200;
}

.employee button {
  background: var(--gold);
  border: 1px solid var(--dark);
  border-radius: 5px;
  cursor: pointer;
  font-weight: bold;
  padding: 10px;
}

.employee button:hover {
  background: var(--dark);
  color: white;
  transition: 0.3s all;
}

main {
  margin: 25px auto;
  width: 80%;
}

.employee-list {
  display: grid;
  grid-gap: 25px;
  gap: 25px;
  grid-template-columns: repeat(3, 1fr);
}

